  color: white;
}
.login-form  .login-container {
    padding-top: 25vh;
    padding-left: 0vw;
    margin: 0;
  }

.login-form .input {
    border: 0;
    background: none;
    display: block;
    margin: 0 auto;
    text-align: center;
    border: 2px solid white;
    padding: 25px 10px;
    width: 260px;
