input[type="radio"] {
  -webkit-appearance:none;
     -moz-appearance:none;
          appearance:none;
  display: inline-block;
  width: 30px;
  height: 30px;
  background: #eeeeee;
  box-shadow:
    inset 0 0 0 .4em white,
    0 0 0 .3em;
  border-radius: 50%;
  transition: .2s;
  cursor:pointer;
  color: #565656;
  }

  input[type="radio"]:hover,
  input[type="radio"]:checked {
    background: #565656;
    box-shadow:
    inset 0 0 0 .6em white,
    0 0 0 .3em;
  }
  
  input[type="radio"]:checked {
    background: #076340;
    box-shadow:
      inset 0 0 0 .4em white,
      0 0 0 .3em #076340;
  } 

  input[type="radio"]:focus { 
   outline: 0; 
  }






